*{padding: 0; margin: 0; overflow: hidden; font-family: 'Roboto', sans-serif;}

.App {
  text-align: center;
  background-color: #181818;
  color: white;
  height: 100vh;
  transition: 300ms;
}

.App-header {
  position: relative;
  background: transparent;
  height: 10vh;
  width: 100vw;
  position: fixed;
  top: 0;
}

.App-header strong, .App-header p{
  display: inline-block;
  margin: 4vh 4vw;
  cursor: pointer;
}

.App-header strong{
  font-weight: normal;
  position: absolute;
  left: 0;
}
.user{
  background: transparent;
  border: none;
  color: white;
  position: absolute;
  right: 0vw;
  top: 0vh;
  margin: 3vh;
  border-radius: 30px;
  transition: 300ms;
}

.user:hover{
  background: rgba(150, 150, 150, 0.624);
  cursor: pointer;
}
.user span:first-child{
  display: inline-block;
  height: 15px;
  width: 15px;
  vertical-align: middle;
  background-image: url(../../assets/arrow.png);
  background-position: center;
  background-size: contain;
  margin-right: 1vw;
  margin-left: 1vw;
  filter: brightness(2);
  transition: 200ms;
}

.toHide{
  transform: rotate(90deg);
}
.toShow{
  transform: rotate(-90deg);
}

#avatar{
  display: inline-block;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-image: url(../../assets/orange.png);
  background-position: center;
  background-size: contain;
  margin-left: 1vw;
  vertical-align: middle;
}


/* Cards */
.cards{
  position: relative;
  width: 178vw;
  height: 55vh;
  margin-top: 15vh;
  transition: 300ms;
}

.card{
  display: inline-block;
  background-size: cover;
  transition: 300ms;
}
.apps_description{
  line-height: 20vh;
}

.focused{
  position: relative;
  height: 55vh;
  width: 56vw;
  margin-right: 5vw;
  margin-left: 5vw;
  bottom: 0;
}
.unfocus{
  position: relative;
  height: 35vh;
  width: 56vw;
  margin-top: 15vh;
  margin-bottom: 5vh;
  bottom: 0;
}
.unfocus p, .unfocus .button{
  display: none;
}
.focused p, .focused .button{
  display: run-in;
}

.previous{
  margin-left: -15vw;
}
.next{
  margin-right: -15vw;
}

.button{
  position: relative;
  border: 2px solid white;
  border-radius: 30px;
  background: transparent;
  color: white;
  padding: 10px 20px;
  font-size: 1.8em;
  font-weight: bolder;
  cursor: pointer;
  transition: 300ms;
  z-index: 5;
  text-decoration: none;
  line-height: 20vh;
}
.button:hover{
  background-color: white;
  color: gray;
}


.card p{
  font-size: 1.6em;
}
.card p:first-child{
  font-size: 3em;
  font-weight: bolder;
}


/* Background images */
.purple{
  background-image: url(../../assets/purple.png);
}
.orange{
  background-image: url(../../assets/orange.png);
}
.blue{
  background-image: url(../../assets/blue.png);
}

/* Footer */
.footer{
  margin-top: 12vh;
}
.footer a{
  margin: 6vh 6vw 6vh 6vw;
}

.selected{
  color: white;
  cursor: pointer;
}
.normal{
  color: rgba(153, 153, 153, 0.6);
  cursor: pointer;
}

/* Slide Buttons */
.slide_btns{
  position: fixed;
  left: 0;
  width: 100vw;
  height: 16vh;
  top: 45vh;
  z-index: 4;
}

.slide_btns span{
  position: fixed;
  display: inline-table;
  height: 12vh;
  width: 6vw;
  background-image: url(../../assets/arrow.png);
  background-size: contain;
  background-repeat: no-repeat;
  filter: brightness(.8);
  border-radius: 50%;
  padding: 5px;
  transition: 200ms;
  cursor: pointer;
}
.next_btn{
  float: right;
  margin-left: 92vw;
  transform: rotate(180deg);
  height: 12vh;
  width: 6vw;
}
.next_btn:hover{
  margin-left: 94vw;
  filter: brightness(5);
}
.previous_btn{
  margin-left: 2vw;
}
.previous_btn:hover{
  margin-left: 0;
  filter: brightness(5);
}

/* Slide card */
.ft{
  margin-left: 22vw;
}
.sd{
margin-left: -38.5vw;
}
.td{
  margin-left: -98vw;
}

/* User card */
.userCard{
  position: fixed;
  top: 0;
  right: 2vw;
  width: 22vw;
  height: 80vh;
  background: #313131;
  z-index: 12;
  transition: 300ms;
  box-shadow: -5px 5px 5px rgba(5, 5, 5, 0.671);
  padding:  12px;
}
.user_img{
  display: block;
  width: 100px;
  height: 100px;
  background-image: url(../../assets/orange.png);
  background-size: contain;
  border-radius: 50%;
  align-self: center;
  align-content: center;
  margin: 8px;
}

.userCard strong{
  font-size: 1.2em;
  line-height: 40px;
}

.userCard p{
  width: 20vw;
  font-size: 1em;
  line-height: 8vh;
}
.userCard hr{
  filter: opacity(.4);
  display: flex;
  width: 22vw;
}

/* EFFECTS */
.hidden{
  margin-top: -120vh;
}
.show{
  margin-top: 8vh;
}

.blured{
  filter: blur(6px);
}