/* HAPPY SURFING */
.happyApp{
    background-image: url(./assets/bg.jpg);
}

.language{
    position: relative;
    top: 0;
    width: 100vw;
    height: 4vh; 
    background-color: rgb(9, 74, 100);
    color: white;
}
.language p, .language a{
    display: inline-block;
    position: relative;
    top: 0vh;
    margin: 0;
    margin-left: 8px;
    color: white;
}

.App_header{
    position: relative;
    width: 100vw;
    height: 26vh;
    background-image: url(./assets/bg-header.png);
    background-position: bottom;
    background-size: auto;
    justify-content: center;
}
.App_header h1{
    font-weight: bolder;
    font-size: 4em;
    position: relative;
    display: inline-block;
    justify-self: center;
    margin-top: 4vh;
}
.App_header p, .App_header b{
    color: white;
    margin: 0;
    line-height: 18px;
    margin-left: 5px;
    vertical-align: middle;
}

.browsers{
    position: relative;
    width: 80vw;
    height: 58vh;
}
.browser{
    display: inline-block;
    position: relative;
    width: 12vw;
    padding: 2vh 2vw 2vh 2vw;
    height: 60vh;
}
.browser strong{
    display: inline-block;
    font-size: .9em;
    color: red;
    width: 170px;
    background: rgba(255, 255, 255, 0.5);
    padding: 10px;
    text-align: center;
}
.browser p{
    margin: 0;
    text-align: left;
    font-size: 14px;
}
.browser a{
    font-size: .8em;
    text-decoration: none;
    color: black;
}
.browser div{
    background: rgba(255, 255, 255, 0.4);
    height: 5vh;
    width: 100%;
    padding: 6px;
}
.like{
    background: #1E88E5;
    border: none;
    color: white;
    font-weight: bold;
    margin-top: 8px;
    padding: 0px 5px;
    line-height: 18px;
    border-radius: 5px;
    height: 22px;
    width: 150px;
}
.like p{
    line-height: 22px;
    vertical-align: middle;
    margin-left: 8px;
}
.like span{
    position: absolute;
    margin-left: -25px;
    display: inline-block;
    width: 22px;
    height: 22px;
    background-image: url(./assets/social.png);
    background-position-y: 1px;
    background-size: 22px;
}
.browser .img{
    display: inline-block;
    width: 80px;
    height: 80px;
    background-image: url(./assets/browsehappy-sprite.png);
    background-repeat: no-repeat;
    background-size: 380px;
}

.happyApp footer{
    position: relative;
    top: -4vh;
    height: 20vh;
    background-image: url(./assets/bg-footer.png);
    background-size: auto;
    background-position: top;
    color: white;
    padding-top: 3vh;
}
.happyApp footer p{
    width: 60vw;
    position: absolute;
    left: 0;
    text-align: left;
}
.titles strong{ 
    line-height: 15px;
    color: rgb(226, 226, 33);
}
.titles strong:first-child{
    margin-left: 4vw;
    margin-right: 60vw;
}
.titles{
    position: absolute;
    left: 0;
}
.amarillo{
    color: rgb(226, 226, 33);
    margin-left: 10px;
}
.icon{
    border-radius: 50%;
    width: 30px;
    height: 30px;
    position: fixed;
    right: 10vw;
    bottom: 3vh;
    display: inline-block;
    background-image: url(./assets/browsehappy-sprite.png);
    background-size: 600px;
    background-repeat: no-repeat;
}
.twitter{
    margin-right: 4vw;
    background-position-x: -288px;
    background-position-y: -130px;
}
.facebook{
    background-position-x: -320px;
    background-position-y: -130px;   
}

/* POSITION OF BROWSERS ICONS */
.GOOGLE{
    background-position-x: 4px;
    background-position-y: 0px;
}
.FIREFOX{
    background-position-x: -74px;
    background-position-y: 0px;
}
.SAFARI{
    background-position-x: -228px;
    background-position-y: 0px;
}
.OPERA{
    background-position-x: -150px;
    background-position-y: 0px;
}
.EXPLORER{
    background-position-x: -300px;
    background-position-y: 0px;
}